.main{
	padding: 0;
	margin: 0;
}

/* -------------------Navbar------------------------ */
nav{
  display: flex;
  /* flex: 1; */
  /* display: block; */
  /* float: right ; */
  justify-content: space-between;
  /* justify-content: left; */
  align-items: center;
  height: 9vh;
  background-color: rgb(41, 40, 40);
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  /* position: sticky; */
  position: fixed;
  width: 99.2%;
  /* position: absolute; */
  overflow: hidden;
  z-index: 100;
  cursor: pointer;
  /* padding-right: 0.5rem; */
  /* background: cover; */
}

.logo{
  justify-content: left;
  padding-left: 5rem;
  overflow: auto;
}

.logo img{
  float: left;
  height: 3rem;
  margin-right: 1rem;
}
.logo h4{
  text-transform: uppercase;
  color: white !important;
  letter-spacing: 5px;
  font-size: 2.5rem;
  text-decoration: none !important;
  float: left;
}
.logo:hover{
  transform: scale(1.3);
}

.nav-links{
  display: flex;
  /* flex: 0.33; */
  justify-content: space-around;
  width: 30%;
  color: #fcfafd;
  /* margin-right: 15rem; */
  /* text-decoration: underline; */
  margin-left: 15rem;
	margin-right: 5rem;
}
.nav-links li{
  list-style: none;
  color: #eee;
}
.nav-a:link, .nav-a:visited {
color: #eee;
}
.nav-a{
  color: #eee;
}
.nav-links a{
  color: #eee;
  text-decoration: none;
  letter-spacing: 3px;
  font-weight: bold;
  font-size: 14px;
}
.nav-a:hover{
   color: #eee;
}
.nav-a:active{
  color: #eee;
}



