/* -------------------Webpage Body------------------------ */

/* -------------------Banner-Title------------------------ */

.banner-heap{
	margin-top: 0;
	background-image:url("../images/heapsort-banner.jpg") !important;
	height: 600px;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	width: 100%;
}

