	color: #1d2d50;
	text-align: left;
	margin-top: 0;
	font-size: 130px;
	margin-left: 5rem;
	width: 25%;
	padding-left: 30px;
	padding-top: 150px;
	font-family: 'Ubuntu', sans-serif;
	font-weight: bolder;

