/* -------------------Code------------------------ */

.code{
	background-image:linear-gradient(to right bottom,rgba(255,255,255,0.3), rgba(210, 215, 211, 0.3)) ,url("../images/bg-blue.jpg");
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	padding: 80px;
}
.code-header{
	background-color: #10316b;
}
.code-card{
	background-color: #14274e;
	color: #f1f6f9;
}
.tab {
  overflow: hidden;
  border: 1px solid #ccc;
  background-color: #f1f1f1;
}


/* -------------------Code Drawer------------------------ */

/* Style the buttons that are used to open the tab content */
.tab button {
  background-color: inherit;
  float: left;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 14px 16px;
  transition: 0.3s;
}

/* Change background color of buttons on hover */
.tab button:hover {
  background-color: #ddd;
}
.tab:hover{
box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.9), 0 6px 20px 0 rgba(0, 0, 0, 0.29);
}
/* Create an active/current tablink class */
.tab button.active {
  background-color: #ccc;
}

/* Style the tab content */
.tabcontent {
  display: none;
  padding: 1rem 1.5rem;
  border: 0.2rem solid #ccc;
  border-top: none;
  background-color: white;
  font-size: 2rem;
  font-weight: bold;
  line-height: 1.5rem;
	text-align: left;
}

.tablinks{
	font-size: 25px;
	font-weight: bolder;
	color: #14274e;
}
